



























import Vue from "vue";
import ProjectDetailsOverlay from "@/components/ProjectDetailsOverlay.vue";
import ProjectData from "@/data/ProjectData.ts";

export default Vue.extend({
  name: "ProjectsList",
  components: {
    ProjectDetailsOverlay,
  },
  props: {
    projects: Array as () => ProjectData[],
  },
  data: function () {
    return {
      showPopup: false,
      popupTitle: "",
      popupColor: "",
      popupContent: ""
    };
  },
  mounted() {
    if (this.$route.query.project) {
      const project = this.projects.find(p => p.id === this.$route.query.project) ;
      if (project) {
        this.showDetails(project);
      }
    }
  },  
  methods: {
    showDetails: function (item: ProjectData) {      
      // if (event) {
      //   alert(event.target);
      // }
      this.popupTitle = item.name;
      this.popupColor = item.accentColor;
      this.popupContent = item.htmlDescription;
      this.showPopup = true;
      this.$router.push({ path: this.$route.path, query: { project: item.id } });
      window.scrollTo(0,0);
    },
    OnDetailsClosed: function () {
      this.showPopup = false
      this.$router.push({ path: this.$route.path });
    },
  },
});
